<script>
	import axios from 'axios'
	import { InsideSpinner } from '../../../components/InsideSpinner'

	var AllUsers = {
		components:	{
			InsideSpinner
		},
		data() {
			return {
				requestedHeaders: {
					headers: {
						Authorization: process.env.VUE_APP_AUTHORIZATION,
						'x-access-token': localStorage.getItem("auth_token")
					}
				},
				filter:	{
					searchVal: null,
					startDate: null,
					endDate: null,
				},
				dateFormat: {
					input: 'DD MMM YYYY',
				},
				paginationData:	{
					totalResultsRows: 1,
					currentPage: 1,
					perPage: 5,
					resultStart: 1,
					resultEnd: 1,
					totalPages: 1,
				},
				paginationCount: 10,
				allUsers: [],
				usersArr: {},
				search: {},
				activeStatus: 'all',
				statusCount:	{
					all: null,
					active: null,
					pending: null,
					incomplete: null,
					banned: null,
					freeze: null,
					approved: null,
					rejected: null,
				},
				isSearchActive: false,
				isDatePickerActive: false,
			}
		},
		async created() {
			let vm = this
			vm.toggleLoader(true);
			// await vm.getAllUsers()
			vm.getEachTotalUsers();
    	// await vm.totalUsers()
			await vm.getUserList(1);
		},
		computed: {
			paginateTotalPages: function() {
				let vm = this
				if(vm.paginationData.totalPages > vm.paginationCount){
					let pagiArr = [];
					let chunkArr = vm._.chunk([...Array(vm.paginationData.totalPages).keys()], 10);
					pagiArr = chunkArr[vm._.findIndex(chunkArr, function(el) { return el.includes(vm.paginationData.currentPage-1)})];
					pagiArr = pagiArr.map(v => v+1);
					if(pagiArr[pagiArr.length - 1] < vm.paginationData.totalPages){
						pagiArr.push( pagiArr[pagiArr.length - 1] + 1 );
					}
					return pagiArr;
				}else{
					return vm.paginationData.totalPages;
				}
			}
    },
		methods: {
			decodeJwt(paramToken) {
				const b64DecodeUnicode = str =>
				decodeURIComponent(
					Array.prototype.map.call(atob(str), c =>
					'%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
				).join(''));
				const parseJwt = token =>
				JSON.parse(
					b64DecodeUnicode(token.split('.')[1].replace('-', '+').replace('_', '/'))
				);
				return parseJwt(paramToken)
			},
			/** Toggle Parent Loader */
			toggleLoader(opt, msg){
				let vm = this
				vm.$parent.toggleLoader(opt, msg);
			},
			async submitFilter(page) {
				let vm = this

				if(vm.filter.searchVal == null && vm.filter.startDate == null && vm.filter.endDate == null){
					vm.$swal("Error!", "Seems like you forgot to put something on search filters!", 'error');
					return false;
				}
				if(vm.filter.startDate == null && vm.filter.endDate){
					vm.$swal("Error!", "Please select a Start Date.", 'error');
					return false;
				}
				if(vm.filter.startDate && vm.filter.endDate == null){
					vm.$swal("Error!", "Please select an End Date.", 'error');
					return false;
				}
				let searchFilterObj = {
					date_from: vm.filter.startDate ? vm.$moment(vm.filter.startDate).format('YYYY-MM-DD') : null,
					date_to: vm.filter.endDate ? vm.$moment(vm.filter.endDate).format('YYYY-MM-DD') : null,
					filter: vm.filter.searchVal,
				}
				if( vm.activeStatus != 'all' ){
					searchFilterObj.filterStatus = vm.getStatusValue(vm.activeStatus);
				}
				console.log(searchFilterObj);
				vm.isSearchActive = true;
				vm.toggleLoader(true, 'Loading data');
				vm.paginationData.currentPage = page !== undefined ? vm.paginationData.currentPage : 1;
				await vm.getUserList(vm.paginationData.currentPage, searchFilterObj)
			},
			async removeFilter() {
				let vm = this
				vm.isSearchActive = false;
				vm.filter =	{
					searchVal: null,
					startDate: null,
					endDate: null,
				};
				vm.isDatePickerActive = false;
				vm.toggleLoader(true, 'Loading data');
				await vm.getUserList(1)
			},
			filterDateChanged()	{
				let vm = this
				vm.isDatePickerActive = true;
			},
			async selectUserStatus(opt)	{
				let vm = this
				vm.activeStatus = opt == 'all' ? 'all' : vm.getStatusValue(opt);
				vm.toggleLoader(true, 'Loading data');
				// await vm.totalUsers();
				if(vm.isSearchActive){
					vm.submitFilter(1);
				}else{
					vm.getUserList(1);
				}
			},
			getStatusValue(status){
				let value = '';
				let numVal = '';
				switch (status) {
					case 0:
					case 'incomplete':
						value = 'incomplete';
						numVal = 0;
						break;
					case 1:
					case 'pending':
						value = 'pending';
						numVal = 1;
						break;
					case 2:
					case 'active':
						value = 'active';
						numVal = 2;
						break;
					case 3:
					case 'banned':
						value = 'banned';
						numVal = 3;
						break;
					case 4:
					case 'rejected':
						value = 'rejected';
						numVal = 4;
						break;
					case 5:
					case 'freeze':
						value = 'freeze';
						numVal = 5;
						break;
					case 6:
					case 'approved':
						value = 'approved';
						numVal = 6;
						break;
					default:
						value = '';
						numVal = null;
						break;
				}
				return typeof status == 'number' ? value : numVal;
			},
			goToUserDetails(data)	{
				let vm = this
				let status = vm.getStatusValue(data.status);
				
				if(status != ''){
					vm.$router.push({ name: 'User Details', params: { id: data._id } });
				}
			},
			async getAllUsers() {
				let vm = this
				let url = `/api/users?skip=0&limit=10000`;
				let results = await axios.get(url, vm.requestedHeaders);
				vm.allUsers = results.data.data;
			},
			async totalUsers(queryStringObj) {
				let vm = this
				try {
					let url = `/api/users?skip=0&limit=10000${ (queryStringObj!==undefined)?`&${Object.keys(queryStringObj)}=${Object.values(queryStringObj)}`:'' }`;
					if(vm.activeStatus != 'all'){
						url += `&status=${vm.getStatusValue(vm.activeStatus)}`;
					}
					let totalRows = await axios.get(url, vm.requestedHeaders)
					vm.paginationData.totalResultsRows = totalRows.data.total;
				} catch (err) {
					console.log(err);
					this.$swal('Error!', err ,'error')
					vm.toggleLoader(false);
				}
			},
			async getUserList(page, queryStringObj) {
				let vm = this
				let skip
				if (vm.paginationData.currentPage == 1) {
					skip = 0
				} else if (vm.paginationData.currentPage == page) {
					skip = (page - 1) * vm.paginationData.perPage
				}
				// if query string object is passed it'll be appended, otherwise no changes
				let url = `/api/users?&skip=${skip}&limit=${vm.paginationData.perPage}`
				if(queryStringObj !== undefined){
					Object.keys(queryStringObj).map(val => queryStringObj[val] != null && (url += `&${val}=${queryStringObj[val]}`) );
				}
				if(vm.activeStatus != 'all'){
					url += `&status=${vm.getStatusValue(vm.activeStatus)}`;
				}
				vm.toggleLoader(true, 'Loading data');
				// Limit display per page
				await axios.get(url, vm.requestedHeaders)
					.then((res)	=>	{
						console.log(res);
						vm.usersArr = res.data;
						// vm._.remove(vm.usersArr.data, function(n) {
						// 	return n.status == 7;
						// });
						vm.paginationData.totalResultsRows = res.data.total;
						vm.paginationData = { ...vm.paginationData, ...{
							resultStart: skip + 1,
							resultEnd: skip + vm.usersArr.data.length,
							totalPages: Math.ceil(vm.paginationData.totalResultsRows / vm.paginationData.perPage),
							currentPage: page,
						}};  
						vm.toggleLoader(false);
					})
					.catch((err)	=>	{
						console.log(err);
						vm.$swal('Error!', err, 'error')
						vm.toggleLoader(false);
					})
			},
			selectPage(page)  {
				let vm = this
				vm.paginationData.currentPage = page;
				if(vm.isSearchActive){
					vm.submitFilter(page);
				}else{
					vm.getUserList(page);
				}
			},
			changePage(opt)  {
				let vm = this
				vm.paginationData.currentPage = opt == 'prev' ? vm.paginationData.currentPage-1 : vm.paginationData.currentPage+1;
				if(vm.isSearchActive){
					vm.submitFilter(vm.paginationData.currentPage);
				}else{
					vm.getUserList(vm.paginationData.currentPage);
				}
			},
			async getEachTotalUsers(){
				let vm = this
				vm.statusCount.all = await vm.getUserStatusCount(null);
				vm.statusCount.active = await vm.getUserStatusCount(2);
				vm.statusCount.pending = await vm.getUserStatusCount(1);
				vm.statusCount.incomplete = await vm.getUserStatusCount(0);
				vm.statusCount.banned = await vm.getUserStatusCount(3);
				vm.statusCount.freeze = await vm.getUserStatusCount(5);
				vm.statusCount.approved = await vm.getUserStatusCount(6);
				vm.statusCount.rejected = await vm.getUserStatusCount(4);
			},
			async getUserStatusCount(status){
				let vm = this
				try {
					let url = `/api/users?skip=0`;
					if(status != null){
						url += `&status=${status}`;
					}
					let totalRows = await axios.get(url, vm.requestedHeaders)
					return totalRows.data.total;
				} catch (err) {
					console.log(err);
					this.$swal('Error!', err ,'error')
					vm.toggleLoader(false);
				}
			},
    }
	}
	export default AllUsers
</script>